.home-container {
    padding: 30px 15px;
    &__title {
        margin-bottom: 1rem;
    }
    &__player-list {
        list-style: none;
        display: flex;
        gap: 1rem;
        flex-direction: column;
        margin-bottom: 1rem;
        li {
            display: flex;
            .trash {
                background-color: white;
                border-radius: 0 5px 5px 0;
            }
        }
    }

    &__button-container {
        display: flex;
        gap: 3rem;
        flex-direction: column;
    }
}